.general_info {
  display: grid;
  grid-template-columns: repeat(2, 50%);
  grid-template-rows: minmax(40px, auto);
  grid-gap: 1em;
  box-sizing: border-box;
  margin-bottom: 50px;
  .input {
    border-radius: 1.5px;
    border: solid 0.5px rgba(177, 167, 200, 0.19);
    background-color: #ffffff;
    width: 100%;
    box-sizing: border-box;
    height: 50px;
    padding: 20px;
    &::placeholder {
      font-family: AvenirNext-Medium;
      font-weight: 500;
      font-style: normal;
      font-stretch: normal;
      line-height: 1.92;
      letter-spacing: 0.03px;
      color: #99abc3;
    }
  }
  .text_area {
    font-size: 15px;
  }
  #upload-container {
    padding: 5%;
    height: 100%;
    box-sizing: border-box;
  }
  .first_position {
    grid-row-start: 2;
  }
  .second_position {
    grid-row-start: 3;
  }
  .third_position {
    grid-row-start: 4;
  }
  .forth_position {
    display: flex;
    flex-direction: column;
    grid-row: 2 / span 3;
    height: 98%;
  }
}
