.common_text_area {
  resize: none;
  border-radius: 1.5px;
  border: solid 0.5px rgba(177, 167, 200, 0.19);
  background-color: #ffffff;
  padding: 10px 15px;
  height: 100px;
  width: 100%;
  box-sizing: border-box;
  font-family: AvenirNext-Medium;
  &::placeholder {
    font-family: AvenirNext-Medium;
    font-size: 15px;
    font-weight: 500;
    font-style: normal;
    font-stretch: normal;
    line-height: 1.92;
    letter-spacing: 0.03px;
    color: #99abc3;
  }
  &:hover {
    border-color: #1ccee9 !important;
  }
}

textarea {
  &:not(:placeholder-shown) {
    border-color: #1ccee9;
  }
}
