.modal_wrapper {
  background-color: rgba(6, 9, 14, 0.69);
  height: 300vh;
  position: fixed;
  top: 0;
  width: 100vw;
  overflow: hidden;
  z-index: 99999;

  .modal_window {
    position: fixed;
    top: 40%;
    left: 35%;
    width: 30%;
    background-color: #ffffff;
    height: 240px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-direction: column;
    z-index: 9999999;

    .modal_header {
      width: 100%;
      height: 16%;
      display: flex;
      align-items: center;
      font-size: 30px;
      justify-content: flex-end;
      color: #ffffff;
      background-image: linear-gradient(265deg, #0072f0, #20bdcb);

      span {
        position: relative;
        right: 20px;
        cursor: pointer;
      }
    }

    .modal_text {
      font-size: 20px;
      font-weight: 530;
      line-height: 1.67;
      letter-spacing: 0.08px;
      text-align: center;
      color: #28252f;
      font-family: AvenirNext-Medium;
    }

    .common_btn {
      margin-block-end: 3%;
    }
  }
}
.common_btn_link {
  margin: 0 auto;
  align-self: center;
}
.modal_delete_advert {
  width: 400px;
  height: 300px;
  background: black;
  position: absolute;
  top: 11%;
  left: 40%;
}
.modal_button_group {
  width: 68%;
  height: 15%;
  padding-bottom: 5%;
  display: flex;
  justify-content: space-between;

  .delete_modal_btn {
    padding: 0;
    height: 100%;
  }

  &btn:hover {
    color: black !important;
  }
}

.link_modal_btn {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  padding-bottom: 1.3%;
  a {
    width: 40%;
  }
}
