  @media only screen and (max-width: 425px) {
    .common_advert {
      height: auto;
      padding-bottom: 5%;
      .left_side_advert {
        width: 84%;
        padding-right: 0;
        h1 {
          font-size: 14px;
          font-weight: 700;
          line-height: 1.36;
          letter-spacing: 0.07px;
          text-align: left;
          color: #28252f;
        }
        .minor_info_advert {
          img {
            width: 15px;
            height: 15px;
          }
          .ispda_advert,
          .date_advert,
          .place_advert {
            font-size: 10px;
            letter-spacing: 0.06px;
            color: #28252f;
            margin-inline-end: 1%;
          }
        }
        p {
          font-size: 12px;
          letter-spacing: 0.06px;
          color: #28252f;
        }
      }

      .right_side_advert {
        width: 22%;
        .fullprice_advert {
          font-size: 15px;
          font-weight: 600;
          letter-spacing: 0.08px;
          color: #20bdcb;
        }
        .image_document_advert{
          width: auto;
          height: auto;
          margin-top: 30%;
        }
      }
      .profile_advert_check_active{
        width: 7px;
        height: 7px;
        top: 5px;
        right: 5px;
      }
    }
  }

