.common-input {
  padding: 5px;
  border-radius: 30px;
  border: solid 0.5px #b1a7c8;
  box-sizing: border-box;
  font-family: AvenirNext-Medium;
  font-size: 15px;
  font-weight: 500;
  font-style: normal;
  font-stretch: normal;
  line-height: 1.67;
  letter-spacing: 0.08px;
  color: #28252f;
  &:hover {
    border-color: #1ccee9!important;
  }
}